@import "global.scss";

.section2_list-variety{
  position: relative;
  font-size: $descr-size;
  &:before{
    position: absolute;
    left: -50px;
    top: 10px;
    content: 'Yoga -';
    color: $second-color;
    font-size: $descr-size;
  }
  &>li{
    margin: 10px 5px;
  }
}

.section2_main-info{
  max-width: 530px;
  &>span{
    color: $second-color;
    font-size: 48px;
  }
}

.section2_title{
  font-size: 24px;
}

.section2_descr{
  font-size: $descr-size;
  line-height: 30px;
}

.section2_distance-descr{
  display: block;
  height: 15px;
}

.section2-send_btn{
  position: relative;
  background: $second-color;
  color: #ffffff;
  border-radius: 28px;
  font-size: 16px;
  padding: 21px 70px;
}

.section_btn-circle{
  position: absolute;
  left: 0px;
  top: 0px;
  width: 60px;
  height: 60px;
  background: #f87b4d;
  border-radius: 50%;
  &:before, &:after{
    content: '';
    position: absolute;
    top: 28px;
    width: 20px;
    height: 2px;
    background: #ffffff;
  }
  &:before{
    left: 20px;
  }
  &:after{
    left: 20px;
    transform: rotate(-90deg);
  }
}