$second-color: #f88971; //цвет спанов
$descr-size: 18px; //размер шрифта для абзацов


*{
  box-sizing: border-box;
}

body{
  width: 100%;
  padding-left: 90px;
  padding-right: 90px;
  font-family: Roboto, Arial, sans-serif;
  background: #FBF5F4;
  color: #000000;
  max-height: 969px;
}

ul,li{
  list-style: none;
  margin: 0;
  padding: 0;
}

a{
  text-decoration: none;
}

button{
  background: none;
  border: none;
  cursor: pointer;
}

.section{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.small-logo{
  height: 30px;
  width: 48px;
}

.big-logo{
  max-height: inherit;
}

.counter{
  display: flex;
  flex-direction: column;
  text-align: right;
}

.counter>span{
  color: $second-color;
  &:first-child{
    font-size: 72px;
  }
  &:last-child{
    font-size: 30px;
    padding-right: 10px;
  }
}